.banner{
  color: #fff;
  height: 80vh;
}

.banner--vertical{
  width: inherit;
  height: inherit;
  background: linear-gradient(to top, rgba(0, 0, 0, 1), transparent 90%);
}

.banner--herzontal{
  width: inherit;
  height: inherit;
  background: linear-gradient(to right, rgba(0, 0, 0, 1), transparent 95%);
  display: flex;
  align-items: center;
}

.banner-text{
  margin: 2rem;
}

.banner-text h1{
  font-size: 48px;
  margin-bottom: 1.5rem;
}

.banner--info{
  display: flex;
  font-size: 18px;
  margin-bottom: 1.5rem;
}

.banner--point{
  color: rgb(94, 160, 124);
  font-weight: bold;
  margin-right: 1rem;
}

.banner--seasons{
  font-weight: bold;
}

.banner--description{
  color: rgb(160, 160, 160);
  font-size: 18px;
  max-width: 500px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  margin-bottom: 1.5rem;
  line-height: 1.5rem;
}

.banner--btns{
  margin: 1.5rem 0;
  display: flex;
}

.banner--btns a{
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  margin-right: 1rem;
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  background-color: rgba(68, 68, 68, 0.486);
  transition: 0.2s;
}

.banner--btns a:hover{
  background-color: #888;
  color: #000;
}

.banner--genres{
  font-size: 18px;
  color: rgb(160, 160, 160);
}

@media only screen and (max-width: 750px){

  .banner-text{
    margin: 1rem;
  }

  .banner-text h1{
    font-size: 36px;
    margin-bottom: 1rem;
  }

  .banner--btns , .banner--description , .banner--info{
    margin-top: 0;
    margin-bottom: 1rem;
  }
}